// 
import UtilIce from '@/libs/util-ice.js'; // 
import Blank from './layout/Blank';

//  routerConfig  iceworks 
import Index from './pages/Index';
import Login from './pages/Login';
import Error401 from './pages/Error401';
import Error404 from './pages/Error404';

//
import rubbish from './router/routerConfigs/rubbish/rubbish';

const routerConfig = [
  {
    path: '/',
    name: 'index',
    layout: Blank,
    component: Index,
  },
];

// 
// ice 
// 
//  routerConfig
const routerConfigMenuOut = [
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: '/401',
    name: 'Unauthorized',
    component: Error401,
    meta: {
      requiresAuth: false,
    },
  },
  {
    path: '*',
    component: Error404,
  },
];

// 
//  router.js 

export const pageRouter = [
  ...rubbish
]

export default UtilIce.recursiveRouterConfig([
  ...pageRouter,
  ...routerConfigMenuOut,
  ...routerConfig
]);


// 
//  mian.js 
export const frameInRoutes = UtilIce.recursiveRouterConfig(routerConfig).map(
  e => {
    const route = e.children ? e.children[0] : e;
    return {
      path: e.path,
      name: route.name,
      meta: route.meta,
    };
  }
);
