<template>
  <div class="workapply-area">
    <d2-container>
      <template slot="header"></template>

      <!---->
      <table-comb
        name=""
        ref="tableMain"
        :search-model-base="tableMainSearchModelBase"
        :get-action="$api.rubbish.list"
        :get-action-where="getActionWhere"
        :removeAction ="$api.rubbish.del"
        :afterFetchData="afterFetchData">
        <!---->
        <template slot="baseSearchForm" slot-scope="scope">
          <el-input
            placeholder=""
            prefix-icon="el-icon-search"
            clearable
            v-model="scope.form.keyword"
            style="width: 250px; margin-left: 10px;">
          </el-input>
          <el-button class="fr ml10" @click="addRubbish"></el-button>
        </template>
        <!---->
        <template slot="tableColumns">
          <el-table-column
            prop="itemName"
            label="">
          </el-table-column>
          <el-table-column
            prop="belongCategory.categoryName"
            label="">
          </el-table-column>
          <el-table-column
            prop="belongCategory.description"
            label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="props">
              {{ props.row.status===1?'':'' }}
            </template>
          </el-table-column>

          <el-table-column label="" width="200px">
            <template slot-scope="props">
              <el-button type="text" size="mini" @click="editRubbish(props.row)"></el-button>
              <el-button type="text" size="mini" @click="delRubbish(props.row)"></el-button>
            </template>
          </el-table-column>
        </template>
      </table-comb>
      <template slot="footer"></template>
    </d2-container>

    <el-dialog :title="dialogVO.id?'':''" :visible.sync="dialogShow">
      <el-form ref="dialogVO" :model="dialogVO" label-width="150px" :rules="rules">
        <el-form-item label="" prop="itemName">
          <el-input style="width: 210px" v-model="dialogVO.itemName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="categoryId">
          <el-select style="width: 210px" v-model="dialogVO.categoryId" placeholder="">
            <el-option
              v-for="item in categoryList"
              :label="item.categoryName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="dialogVO.status"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save"> </el-button>
        <el-button @click="dialogShow = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import listMixin from "@/mixins/list.mixin";
  export default {
    // 
    // name  name 
    name: "rubbishList",
    components: {},
    mixins: [
      listMixin
    ],
    data() {
      return {
        tableMainSearchModelBase:{
          keyword: '',
        },
        //
        categoryList:[],
        dialogShow:false,
        //vo
        dialogVO:{
          id:'',
          categoryId:'',
          itemName:'',
          status:true
        },
        rules: {
          itemName: [
            { required: true, message: ''},
          ],
          categoryId: [
            { required: true, message: ''},
          ],
        }
      };
    },
    computed: {
      getActionWhere(){
        return {

        }
      },
    },
    methods: {
      addRubbish(){
        this.dialogVO= {
          categoryId:'',
          itemName:'',
          status:true
        }
        this.openDialog();
      },
      editRubbish(detailVO){
        this.dialogVO= {
          id:detailVO.id,
          categoryId:detailVO.belongCategory.id,
          itemName:detailVO.itemName,
          status:detailVO.status==1,
        }
        this.openDialog();
      },
      openDialog(){
        if(this.$refs['dialogVO']){
          this.$nextTick(()=>{
            this.$refs['dialogVO'].clearValidate();
          })
        }
        this.dialogShow = true;
      },
      delRubbish(vo){
        this.$confirm('')
          .then(() => {
            this.$api.rubbish.del(vo.id).then(res=>{
              if(res.status==1){
                this.$message.success('');
                this.$refs.tableMain.fetchData();
              }else {
                this.$message.error(res.errorMessage)
              }

            });
          })
          .catch(confirm => {});
      },
      save(){
        this.$refs['dialogVO'].validate((valid) => {
          if (valid) {
            let vo = {
              ...this.dialogVO,
              status: this.dialogVO.status?1:0
            };
            //
            if(!vo.id){
              delete vo.id;
              //
              this.$api.rubbish.create(vo).then(res=>{
                if(res.status==1){
                  this.dialogShow = false
                  this.$message.success('');
                  this.$refs.tableMain.fetchData();
                }else{
                  this.$message.error(res.errorMessage)
                }

              })
            }else{
              this.$api.rubbish.edit(vo).then(res=>{
                if(res.status==1){
                  this.dialogShow = false
                  this.$message.success('');
                  this.$refs.tableMain.fetchData();
                }else{
                  this.$message.error(res.errorMessage)
                }

              })
            }
          } else {
            return false;
          }
        });
      },
      afterFetchData(){

      }
    },
    mounted() {
      //
      this.$api.rubbish.categoryList({}).then(res=>{
        this.categoryList = res.data;
      })
    }
  };
</script>